import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import MoveForward from "../components/common/move-forward";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import PortfolioOne from "../screens/portfolio/portfolio-page";

const PortfolioPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Portfolio", link: null },
  ];
  return (
    <Layout>
      <BreadCrumb items={breadcrumbLinks} />
      <div className="mt-5">
        <PortfolioOne />
        <MoveForward db={db} />
      </div>
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes('new_logo.png');
  });

  return (
    <Seo
      title="Portfolio | Justtry Technologies"
      metaTitle="Justtry Technologies Portfolio"
      description="Explore Justtry’s diverse portfolio of successful projects, highlighting our innovation and expertise in Blockchain and Web development. Discover how our tailored solutions have helped businesses thrive and achieve their objectives with outstanding results."
      image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
    ></Seo>
  );
};

export default PortfolioPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
